#Home{
    padding-top: 100px ;
    color: rgb(23, 23, 23);
    position: relative;
    top: 10px;
    display: flex;
    padding-bottom: 120px;
    background-color: rgb(255, 255, 255);
}
.pic img{
    max-width: 100%;
    height: auto;
    position: relative;
    transition: 0.6s;
}
.pic img:hover{
    transform: scale(1.03);
}

#intro1{
    font-size: 40px;
    margin-left: 20px;
    color: rgb(10, 44, 36);
}
#intro2{
    margin: -40px 0px 0px 20px  ;
    margin-right: -40px;
    font-weight:600;
    font-size: 55px;
    border-right: 8px solid #3e465ab3; /* Adjusted border width for better visibility */
    width: 350px;
    white-space: nowrap;
    overflow: hidden;
    animation: typing 2s ,
    cursor .4s step-end infinite alternate;
}
#intro3{
    font-size: 20px;
    font-family: "Open Sans", sans-serif;
    font-optical-sizing: auto;
    text-align: center;
    width: 90%;
}
#intro3 span{
    color: rgb(77, 21, 181);
    font-weight: 600;
}
.icon{
    padding: 4px;
    position: absolute;
    left: 85%;
    top: 15%;
    height: 150px;
    align-content: center;
    transition: 0.3s;
    width: 40px;
    border-radius: 10px;
    background-color: rgb(39, 39, 43);
    box-shadow: 0px 1px 3px rgb(37, 37, 37);
}
.icon:hover{
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0);
}
.icon {
    padding: 4px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
}

.icon a {
    color: rgb(255, 255, 255);
    font-size: 22px;
    transition: 0.2s;
    text-decoration: none;
    justify-content: center;
    display: flex;
}

.icon a:hover {
    color: rgb(0, 0, 0);
}
#dsa-sec {
    display: flex;
    justify-content: center;
    gap: 15px;
    padding: 15px;
  }
  
  .dsa-btn {
    width: 50px;
    height: 50px;
    border: none;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
  }
  .leetcode-icon-top {
    font-size: 40px;
    color: #FFA500;
    margin: 0 auto 10px;
    position: absolute;
    top: 30px;
    left: 30px;
  }
  
  .leetcode-modal {
    
    background: #222222;
    color: #fff;
    width: 350px;
    padding: 25px;
    border-radius: 10px;
    text-align: center;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-family: "Poppins", sans-serif;
    height: 300px;
}

.leetcode-avatar {
    width: 90px;
    height: 90px;
    border-radius: 50%;
    margin-bottom: 12px;
    border: 3px solid #FFA500;
}

.leetcode-username {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 5px;
}

.leetcode-rank,
.leetcode-solved {
    font-size: 16px;
    margin: 6px 0;
    padding: 10px;
    background-color: #282828;
    text-align: left;
    border-radius: 6px;
    width: 170px;
}

.leetcode-buttons {
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
}

.leetcode-visit {
    background: #FFA500;
    color: #282828;
    border: none;
    padding: 10px 18px;
    cursor: pointer;
    font-weight: bold;
    border-radius: 8px;
    transition: 0.3s;
}

.leetcode-visit:hover {
    background: #ffbf47;
}

.leetcode-close {
    background: #262626;
    color: #FFA500;
    border: 1px solid #FFA500;
    padding: 10px 18px;
    cursor: pointer;
    font-weight: bold;
    border-radius: 8px;
    transition: 0.3s;
}

.leetcode-close:hover {
    background: #333;
}

.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
}

.modal-content {
    background: #262626;
    padding: 25px;
    border-radius: 12px;
    min-width: 320px;
    text-align: center;
    box-shadow: 0px 0px 15px rgba(255, 165, 0, 0.3);
}

.profile-img {
    width: 90px;
    height: 90px;
    border-radius: 50%;
    border: 2px solid #FFA500;
}

.close-btn {
    margin-top: 15px;
    padding: 10px 18px;
    border: none;
    background: #ff4747;
    color: white;
    cursor: pointer;
    border-radius: 8px;
    font-weight: bold;
    transition: 0.3s;
}

.close-btn:hover {
    background: #ff6363;
}

  
#explore-button{
    justify-content: center;
    display: flex;
    font-size: 15px;
}
#explore1{
    background-color:rgb(29, 18, 62) ;
    border: 2px solid black;
    color: rgb(255, 255, 255);
    padding: 10px 22px;
    transition: 0.4s;
    margin: 3px;
    border-radius: 25px;
}
#explore1:hover{
    color: aliceblue;
    background-color: rgb(0, 0, 0);
    border: 2px solid rgba(237, 237, 237, 0);
}
#explore2{
    margin: 5px;
    background-color:rgb(255, 255, 255) ;
    border: 1.5px solid black;
    color: rgb(0, 0, 0);
    padding: 10px 18px;
    transition: 0.4s;
    margin: 3px;
    border-radius: 25px;
}
#explore2:hover{
    color: rgb(20, 10, 42);
    background-color: rgba(229, 223, 255, 0.5);
    border: 1.5px solid rgba(255, 255, 255, 0);
}


@media only screen and (max-width: 600px) {

    #Home{
        display: inline-block;
    }
    .pic img{
        max-width: 280px;
    }
    #intro1{
        font-size: 30px;
        margin-top: -10px;
    }
    #intro3{
        font-size: 17px;
    }
    #intro3 span{
        font-weight: 800;
    }
    #intro2{
        margin: -30px 0px 0px 20px  ;
        font-size: 45px;
        width: 270px;
    }
    
    .icon{
        padding: 4px;
        position: absolute;
        left: 80%;
        top: 19%;
        height: 200px;
        align-content: center;
        width: 40px;
        gap: 10px;
    }
    .icon a{
        color: rgb(255, 255, 255);
        font-size: 25px;
        margin: 15px 0px;
    }
    #explore1{
        font-size: 15px;
    }
    #explore2{
        font-size: 15px;
    }
  }

  @keyframes typing {
    from {
      width: 0;
    }
  }